
export default class RgbColor {
  constructor(rgb) {
    /*
    <xsd:complexType name="CT_RgbColor">
      <xsd:attribute name="rgb" type="ST_UnsignedIntHex" use="optional"/>
    </xsd:complexType>
    */
      this.rgb =rgb;  //UnsignedIntHex
  }
}



